:root {
  --color-primary: #89bf04;
  --color-secondary: #9012fe;
  --color-info: #4990e2;
  --color-warning: #ff6060;
  --color-danger: #f00;

  --color-primary-hover: lighten(--color-primary, .5%);

  --color-post: #49cc90;
  --color-get: #61affe;
  --color-put: #fca130;
  --color-delete: #f93e3e;
  --color-head: #9012fe;
  --color-patch: #50e3c2;
  --color-disabled: #ebebeb;
  --color-options: #0d5aa7;

  // Base Colors
  --black: #000;
  --white: #fff;
  --gray-50: lighten(#000, 92%); //#ebebeb
  --gray-200: lighten(#000, 62.75%); // #a0a0a0
  --gray-300: lighten(#000, 56.5%); // #909090
  --gray-400: lighten(#000, 50%); // #808080
  --gray-500: lighten(#000, 43.75%); // #707070
  --gray-600: lighten(#000, 37.5%); // #606060
  --gray-650: lighten(#000, 33.3%); // #555555
  --gray-700: lighten(#000, 31.25%); // #505050
  --gray-800: lighten(#000, 25%); // #404040
  --gray-900: lighten(#000, 18.75%); // #303030

  --cod-gray: #1b1b1b;
  --agate-gray: #333333;
  --bright-gray: #3b4151;
  --mako-gray: #41444e;
  --waterloo-gray: #7d8492;
  --alto-gray: #d9d9d9;
  --mercury-gray: #e4e4e4;
  --concrete-gray: #e8e8e8;
  --alabaster: #f7f7f7;
  --apple-green: #62a03f;
  --green-haze: #009d77;
  --japanese-laurel: #008000;
  --persian-green: #00a0a7;
  --geyser-blue: #d8dde7;
  --dodger-blue: #1391ff;
  --endeavour-blue: #005dae;
  --scampi-purple: #55a;
  --electric-violet: #7300e5;
  --persian-red: #cf3030;
  --mango-tango: #e97500;

  // Authorize

  --auth-container-border-color: var(--gray-50);
  --auth-select-all-none-link-font-color: var(--color-info);
  // Buttons

  --btn-background-color: transparent;
  --btn-border-color: var(--gray-400);
  --btn-font-color: inherit;
  --btn-box-shadow-color: var(--black);

  --btn-authorize-background-color: transparent;
  --btn-authorize-border-color: var(--color-post);
  --btn-authorize-font-color: var(--color-post);
  --btn-authorize-svg-fill-color: var(--color-post);

  --btn-cancel-background-color: transparent;
  --btn-cancel-border-color: var(--color-warning);
  --btn-cancel-font-color: var(--color-warning);

  --btn-execute-background-color: transparent;
  --btn-execute-border-color: var(--color-info);
  --btn-execute-font-color: var(--white);
  --btn-execute-background-color-alt: var(--color-info);

  --expand-methods-svg-fill-color: var(--gray-500);
  --expand-methods-svg-fill-color-hover: var(--gray-800);

  // Errors

  --errors-wrapper-background-color: var(--color-delete);
  --errors-wrapper-border-color: var(--color-delete);

  --errors-wrapper-errors-small-font-color: var(--gray-600);

  // Form

  --form-select-background-color: var(--alabaster);
  --form-select-border-color: var(--mako-gray);
  --form-select-box-shadow-color: var(--black);

  --form-input-border-color: var(--alto-gray);
  --form-input-background-color: var(--white);

  --form-input-invalid-border-color: var(--color-delete);
  --form-input-invalid-background: lighten(#f93e3e, 35%);

  --form-textarea-background-color: var(--white);
  --form-textarea-focus-border-color: var(--color-get);

  --form-textarea-curl-background-color: var(--mako-gray);
  --form-textarea-curl-font-color: var(--white);

  --form-checkbox-label-font-color: var(--gray-900);
  --form-checkbox-background-color: var(--concrete-gray);
  --form-checkbox-box-shadow-color: var(--concrete-gray);

  // Information

  --info-code-background-color: var(--black);
  --info-code-font-color: var(--color-head);

  --info-link-font-color: var(--color-info);
  --info-link-font-color-hover: darken(#4990e2, 15%);

  --info-title-small-background-color: var(--waterloo-gray);

  --info-title-small-pre-font-color: var(--white);

  // Layout

  --opblock-border-color: var(--black);
  --opblock-box-shadow-color: var(--black);

  --opblock-tag-border-bottom-color: var(--bright-gray);
  --opblock-tag-background-color-hover: var(--black);

  --opblock-tab-header-tab-item-active-h4-span-after-background-color: var(--gray-400);

  --opblock-isopen-summary-border-bottom-color: var(--black);

  --opblock-isopen-section-header-background-color: var(--white);
  --opblock-isopen-section-header-box-shadow-color: var(--black);

  --opblock-summary-method-background-color: var(--black);
  --opblock-summary-method-font-color: var(--white);
  --opblock-summary-method-text-shadow-color: var(--black);

  --operational-filter-input-border-color: var(--geyser-blue);

  --tab-list-item-first-background-color: var(--black);

  --response-col-status-undocumented-font-color: var(--gray-300);

  --response-col-links-font-color: var(--gray-300);

  --opblock-body-background-color: var(--agate-gray);
  --opblock-body-font-color: var(--white);

  --scheme-container-background-color: var(--white);
  --scheme-container-box-shadow-color: var(--black);

  --server-container-background-color: var(--white);
  --server-container-box-shadow-color: var(--black);

  --server-container-computed-url-code-font-color: var(--gray-400);

  --loading-container-before-border-color: var(--gray-650);
  --loading-container-before-border-top-color: var(--black);

  --response-content-type-controls-accept-header-select-border-color: var(--japanese-laurel);
  --response-content-type-controls-accept-header-small-font-color: var(--japanese-laurel);

  // Modal

  --dialog-ux-backdrop-background-color: var(--black);


  --dialog-ux-modal-background-color: var(--white);
  --dialog-ux-modal-border-color: var(--gray-50);
  --dialog-ux-modal-box-shadow-color: var(--black);

  --dialog-ux-modal-content-font-color: var(--mako-gray);

  --dialog-ux-modal-header-border-bottom-color: var(--gray-50);

  // Models

  --model-deprecated-font-color: var(--gray-200);

  --model-hint-font-color: var(--gray-50);
  --model-hint-background-color: var(--black);

  --section-models-border-color: var(--bright-gray);

  --section-models-isopen-h4-border-bottom-color: var(--section-models-border-color);

  --section-models-h4-font-color: var(--gray-600);
  --section-models-h4-background-color-hover: var(--black);

  --section-models-h5-font-color: var(--gray-500);

  --section-models-model-container-background-color: var(--black);

  --section-models-model-box-background-color: var(--black);

  --section-models-model-title-font-color: var(--gray-700);

  --prop-type-font-color: var(--scampi-purple);

  --prop-format-font-color: var(--gray-600);

  // Tables

  --table-thead-td-border-bottom-color: var(--bright-gray);

  --table-parameter-name-required-font-color: var(--color-danger);

  --table-parameter-in-font-color: var(--gray-400);

  --table-parameter-deprecated-font-color: var(--color-danger);

  // Topbar

  --topbar-background-color: var(--cod-gray);

  --topbar-link-font-color: var(--white);

  --topbar-download-url-wrapper-element-border-color: var(--apple-green);

  --topbar-download-url-button-background-color: var(--apple-green);
  --topbar-download-url-button-font-color: var(--white);

  // Type

  --text-body-default-font-color: var(--bright-gray);
  --text-code-default-font-color: var(--bright-gray);
  --text-headline-default-font-color: var(--bright-gray);
}
